@import url(https://fonts.googleapis.com/css?family=Montserrat:500,600&display=swap);
.info-panel__item{min-height:31px;overflow-x:hidden}.info-panel__item:nth-child(odd){background-color:#F4F2EE}.info-panel__item:nth-child(even){background-color:#fff}

.button{padding:8px 15px;margin:15px 0;height:35px;outline:none;-webkit-transition:all .5s ease;transition:all .5s ease}.button:hover{cursor:pointer}

.product-item{position:relative}.product-item:hover:before{content:'';width:100%;height:89%;position:absolute;background:rgba(255,188,0,0.1);background-blend-mode:multiply;border-radius:8px;border-bottom-left-radius:0;border-bottom-right-radius:0}.product-item:hover .product-item__image-block{background-color:transparent}.product-item:hover .info-panel__item:last-child{background-color:#FFF9E6;border-bottom-right-radius:8px;border-bottom-left-radius:8px}.product-item:first-child .info-panel{width:255px}.product-item:first-child .info-panel__item{padding-left:19px}.product-item:last-child .info-panel__item{padding-right:19px}.product-item__image-block{width:183px;height:193px;display:flex;justify-content:center;align-items:center;border-radius:10px;background-color:#F4F2EE;padding:16px;margin:0 19px;z-index:0}.product-item__price{width:100%;display:grid;grid-template-columns:repeat(2, 1fr);align-items:center}.product-item__title{padding:14px 45px 25px 19px}.product-item__rating{display:flex;align-items:center;margin-top:-6px}.product-item__rating i{font-size:25px}.product-item__rating span{margin-top:5px;margin-left:15px}.product-item .info-panel__item{display:flex;align-items:center;padding:0;padding-left:19px}.product-item__link span{color:#0000EE !important}.product-item__actions{width:100%;display:flex;justify-content:space-between}.product-item__actions__basket-svg{margin-right:10px;width:20px}.product-item__actions .button:first-child:hover,.product-item__actions .button:last-child:hover{opacity:.5}.product-item__actions .button:first-child{display:flex;justify-content:center}.product-item__actions .button:last-child{display:flex;align-items:center;justify-content:center;width:117px;margin-right:20px}

*{margin:0;padding:0}.img-responsive{width:100%;height:auto}html,body{font-family:'Montserrat', sans-serif;height:100%}body{display:flex;justify-content:center;align-items:center}.app-main{display:flex;justify-content:center;align-items:center;height:100%;width:100vw}.app-main__sidebar{align-self:flex-end;width:100%;max-width:234px}.app-main__sidebar .info-panel{width:100%;margin-bottom:84px}.app-main__sidebar .info-panel__item{padding-left:11px}.app-main__sidebar .info-panel__item span{vertical-align:sub}.app-main__product-list{display:flex;justify-content:space-between;overflow-x:scroll;width:960px;height:100%}

